.courses {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    height: 100vh;
    transition: margin-top 0.3s ease-out;
}

.dropdown-open {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
}

.sidebox {
    flex-grow: 1;
    flex-basis: 200px;
}

.filter-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    top: 0;
}